<template>
  <form @submit.prevent="submit">
    <div class="grid grid-cols-1 gap-6">
      <div>
        <Input
          :model-value="data.name"
          @update:model-value="handleUpdateData('name', $event)"
          label="Agent Name"
          placeholder="Agent name"
          :view="validation.name.$invalid ? 'danger' : 'default'"
          block
        />
        <small
          v-for="(error, errorIndex) of validation.name.$errors"
          :key="errorIndex"
          class="block text-red-04 my-2"
        >
          {{ error.$message }}
        </small>
      </div>
      <div>
        <Input
          :model-value="data.email"
          @update:model-value="handleUpdateData('email', $event)"
          label="Agent Email"
          placeholder="Agent email"
          :view="validation.email.$invalid ? 'danger' : 'default'"
          block
        />
        <small
          v-for="(error, errorIndex) of validation.email.$errors"
          :key="errorIndex"
          class="block text-red-04 my-2"
        >
          {{ error.$message }}
        </small>
      </div>
      <div>
        <Button dense color="teal">Save</Button>
      </div>
    </div>
  </form>
</template>

<script>
import { computed, defineComponent, ref } from 'vue'
import { useForm } from 'composable/helper/form'

import Input from 'bbUi/components/input/input.vue'
import Button from 'bbUi/components/button/button.vue'
import InputPhone from 'components/InputPhone.vue'

import { email, helpers, required } from '@vuelidate/validators'

export default defineComponent({
  name: 'BulkQuoteSalesPersonForm',
  components: {
    Input,
    Button,
    InputPhone
  },
  props: {
    data: {
      type: Object
    }
  },
  emits: ['update:data', 'submit'],
  setup(props, { emit }) {
    const formData = ref({
      name: null,
      email: null
    })

    const data = computed({
      get: () => (props.data && props.data) || formData.value,
      set: (value) => {
        emit('update:data', value)
        formData.value = value
      }
    })

    const submit = () => {
      const hasInvalidField = !validate()
      if (hasInvalidField) return
      emit('submit', data.value)
    }
    const handleUpdateData = (key, value) => {
      data.value = { ...data.value, [key]: value }
    }

    const rules = {
      name: {
        required: helpers.withMessage('Agent name is required', required)
      },
      email: {
        required: helpers.withMessage('Agent email is required', required),
        email: helpers.withMessage('Please enter a valid email address', email)
      }
    }
    const { validation, validate } = useForm(data, rules)

    return {
      data,
      handleUpdateData,
      submit,
      validation
    }
  }
})
</script>

<style scoped lang="scss">
.bulk-quote-customer-form {
}

:deep(.btn-dense) {
  @apply px-4 #{!important};
}
</style>
