<template>
  <div class="bulk-quote-customer">
    <transition name="slide-fade">
      <div v-if="isSubmitted">
        <div>
          <div class="grid gap-1">
            <p>
              {{ salesPersonData.name }}
            </p>
            <p>{{ salesPersonData.email }}</p>
          </div>
          <Button
            @click="handleEditCustomerData"
            class="mt-6 mb-2"
            color="teal"
            view="outlined"
            dense
          >
            Edit Agent Info
          </Button>
        </div>
      </div>
      <div v-else>
        <BulkQuoteSalesPersonForm
          ref="quoteSalesPersonForm"
          v-model:data="salesPersonData"
          @submit="isSubmitted = true"
        />
      </div>
    </transition>
  </div>
</template>

<script>
import { computed, defineComponent, onMounted, ref } from 'vue'

import Button from 'bbUi/components/button/button.vue'
import CustomerSearch from 'components/Customer/CustomerSearch.vue'
import BulkQuoteSalesPersonForm from 'components/BulkQuote/SalesPerson/BulkQuoteSalesPersonForm.vue'

export default defineComponent({
  name: 'BulkQuoteSalesPerson',
  components: {
    Button,
    CustomerSearch,
    BulkQuoteSalesPersonForm
  },
  props: {
    data: {
      type: Object,
      default: () => ({})
    }
  },
  emits: ['update:data', 'submit'],
  setup(props, { emit }) {
    const salesPersonData = computed({
      get: () => props.data,
      set: (value) => {
        emit('update:data', value)
      }
    })
    const quoteSalesPersonForm = ref()
    const isSubmitted = ref(false)
    const handleUpdateCustomerData = (formData) => {
      salesPersonData.value = formData
    }
    const handleEditCustomerData = () => {
      isSubmitted.value = false
    }

    onMounted(() => {
      isSubmitted.value =
        salesPersonData.value.name && salesPersonData.value.email
    })

    return {
      salesPersonData,
      isSubmitted,
      quoteSalesPersonForm,
      handleUpdateCustomerData,
      handleEditCustomerData
    }
  }
})
</script>

<style scoped lang="scss">
.bulk-quote-customer {
  &__add-customer-button {
    @apply flex flex-wrap items-center w-full hover:bg-grey-lighten  focus:bg-grey-lighten p-3;
  }
}
</style>
