<template>
  <div class="table-item">
    <div class="table-item__container">
      <table class="w-full border-collapse mb-10">
        <thead>
          <tr class="bg-grey-lighten text-black font-normal text-sm">
            <th class="text-left max-w-[200px]">Device</th>
            <th class="text-left">Qty</th>
            <th
              v-for="(condition, index) in conditionHeaders"
              :key="index"
              class="min-w-[70px] max-w-[100px] text-left truncate"
            >
              {{ condition }}
            </th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="item in normalizedItems" :key="item.name">
            <td class="font-semibold text-black text-left max-w-[200px] min-w-[200px]">{{ item.name }}</td>
            <td class="text-grey-03 font-normal">{{ item.qty }}</td>
            <td
              v-for="(condition, index) in item.conditions"
              :key="index"
              class="min-w-[70px] max-w-[100px] text-grey-03 font-normal"
            >
              <Currency :value="condition.price" />
            </td>
          </tr>
        </tbody>
        <tfoot>
          <tr class="border-t-2 border-teal-01 text-grey-03 font-normal">
            <td class="text-left">Total</td>
            <td class="text-left">{{ calculateTotalQty() }}</td>
            <td
              v-for="(condition, index) in conditionHeaders"
              :key="index"
              class="text-left"
            >
              <Currency :value="calculateColumnTotal(index)" />
            </td>
          </tr>
        </tfoot>
      </table>
    </div>
  </div>
</template>

<script>
import { computed } from 'vue'
import Currency from 'components/Currency.vue'

export default {
  components: {
    Currency
  },
  props: {
    items: {
      type: Array,
      default: () => []
    },
    itemNameKey: {
      type: [String, Function],
      default: 'name'
    },
    itemQtyKey: {
      type: [String, Function],
      default: 'qty'
    },
    conditionsKey: {
      type: [String, Function],
      default: 'conditions'
    },
    conditionNameKey: {
      type: [String, Function],
      default: 'name'
    },
    conditionPriceKey: {
      type: [String, Function],
      default: 'price'
    },
    currency: {
      type: String,
      default: 'USD'
    }
  },
  setup(props) {
    const mapItem = (item) => ({
      name:
        typeof props.itemNameKey === 'function'
          ? props.itemNameKey(item)
          : item[props.itemNameKey],
      qty:
        typeof props.itemQtyKey === 'function'
          ? props.itemQtyKey(item)
          : item[props.itemQtyKey],
      conditions: (
        (typeof props.conditionsKey === 'function'
          ? props.conditionsKey(item)
          : item[props.conditionsKey]) || []
      ).map((condition) => ({
        name:
          typeof props.conditionNameKey === 'function'
            ? props.conditionNameKey(condition)
            : condition[props.conditionNameKey],
        price:
          typeof props.conditionPriceKey === 'function'
            ? props.conditionPriceKey(condition)
            : condition[props.conditionPriceKey]
      }))
    })

    const items = computed(() => props.items.map(mapItem))

    const conditionHeaders = computed(() => {
      const allConditions = new Set()
      
      items.value.forEach(item => {
        item.conditions.forEach(condition => {
          allConditions.add(condition.name)
        })
      })
      
      return Array.from(allConditions)
    })

    const normalizedItems = computed(() => {
      return items.value.map(item => {
        const normalizedConditions = conditionHeaders.value.map(headerCondition => {
          const existingCondition = item.conditions.find(condition => condition.name === headerCondition)
          return {
            name: headerCondition,
            price: existingCondition ? existingCondition.price : 0
          }
        })
        
        return {
          ...item,
          conditions: normalizedConditions
        }
      })
    })

    const calculateTotalQty = () => {
      return normalizedItems.value.reduce((total, item) => total + Number(item.qty), 0)
    }

    const calculateColumnTotal = (conditionIndex) => {
      return normalizedItems.value.reduce((total, item) => {
        const condition = item.conditions[conditionIndex]
        return total + (condition ? condition.price * Number(item.qty) : 0)
      }, 0)
    }

    return {
      normalizedItems,
      conditionHeaders,
      calculateTotalQty,
      calculateColumnTotal
    }
  }
}
</script>

<style lang="scss" scoped>
th {
  padding: 8px;
}
td {
  padding: 14px 8px;
}

th {
  font-weight: 400;
}
</style>