<template>
  <div class="grid gap-2">
    <Checkbox
      v-model="isUse"
      class="font-semibold"
      @change="handleChange">
      Additional Shipping Label
    </Checkbox>
    <div class="pl-9">
      <transition name="slide-fade">
        <div v-if="isUse">
          <Input
            v-model="data.add_shipping_label_number"
            type="number"
            block
            ltr
            :view="validation.add_shipping_label_number.$error ? 'danger' : 'default'"
            @input="handleInput"
            @change="handleChangeInput">
            <template #label>
              <span>Number of Labels</span>
            </template>
          </Input>
          <p class="text-right">
            <small
              v-for="(error, n) in validation.add_shipping_label_number.$errors"
              :key="n"
              class="text-red-04">
              {{ error.$message }}
            </small>
          </p>
          <div
            v-if="!validation.add_shipping_label_number.$error"
            class="flex items-center justify-between">
            <small class="text-grey-02">
              Labels will be created according to the quantity you enter.
            </small>
            <small
              v-if="Number(availableLabelLeft) > 0"
              class="text-grey-02 text-xs block">
              {{ availableLabelLeft }}
              {{ availableLabelLeft > 1 ? `labels` : `label` }}
              left to generate.
            </small>
            <small v-else class="block text-red-04">
              {{ 'Has reached maximum limit' }}
            </small>
          </div>
        </div>
      </transition>
    </div>
  </div>
</template>

<script>
import { computed, defineComponent, watch, ref } from 'vue'
import { required, minValue, maxValue, integer } from "@vuelidate/validators"
import { useVuelidate } from '@vuelidate/core'

import Checkbox from 'bbUi/components/checkbox/checkbox.vue'
import Input from 'bbUi/components/input/input.vue'

export default defineComponent({
  name: 'BulkQuoteAdditionalShippingLabel',
  components: {
    Input,
    Checkbox
  },
  props: {
    modelValue: {
      type: [Number, String],
      default: 0
    },
    isUseAdditionalShippingLabel: {
      type: Boolean,
      default: false
    },
    limitExceed: {
      type: [Number, String],
      default: 0
    }
  },
  emits: ['update:modelValue', 'update:isUseAdditionalShippingLabel'],
  setup(props, { emit }) {
    const data = ref({
      add_shipping_label_number: 0
    })
    const isUse = computed({
      get: () => props.isUseAdditionalShippingLabel,
      set: (val) => emit('update:isUseAdditionalShippingLabel', val)
    })
    const rulesInput = computed(() => ({
      add_shipping_label_number: isUse.value ? {
        required,
        integer,
        minValue: minValue(1),
        maxValue: maxValue(props.limitExceed)
      } : {}
    }))
    const validation = useVuelidate(rulesInput, data, { $lazy: true })
    const availableLabelLeft = computed(() => {
      return (props?.limitExceed ?? 1) - data.value.add_shipping_label_number
    })
    const handleChange = () => {
      data.value.add_shipping_label_number = 1
      emit('update:modelValue', data.value.add_shipping_label_number)
    }
    const handleInput = async () => {
      await validation.value?.$validate()
      emit('update:modelValue', data.value.add_shipping_label_number)
    }
    const handleChangeInput = async (e) => {
      const val = Number(e?.target?.value ?? 1)
      const limit = Number(props?.limitExceed ?? 1)
      if (val <= 0) {
        data.value.add_shipping_label_number = 1
      } else if (val > limit) {
        data.value.add_shipping_label_number = limit
      }
      emit('update:modelValue', data.value.add_shipping_label_number)
    }

    watch(() => props.modelValue, () => {
      data.value.add_shipping_label_number = props.modelValue
    })

    return {
      isUse,
      data,
      validation,
      handleInput,
      availableLabelLeft,
      handleChange,
      handleChangeInput
    }
  }
})
</script>

<style scoped lang="scss"></style>

