<template>
  <div class="bulk-quote-customer">
    <transition name="slide-fade">
      <div v-if="isSubmitted">
        <div>
          <div class="grid gap-1">
            <p>
              {{ customerData.full_name }}
              <span class="text-grey-02">({{ customerData.email }})</span>
            </p>
            <p>{{ customerData.company }}</p>
            <p>{{ formattedPhoneNumber }}</p>
            <p>{{ customerData.shipping_address_text }}</p>
            <p>{{ formattedExpectedDateQuote }}</p>
          </div>
          <div class="flex flex-wrap items-center gap-3 mt-6 mb-2">
            <Button
              @click="handleEditCustomerData"
              color="teal"
              view="outlined"
              dense
              >Edit Customer Info
            </Button>
            <Button
              @click="handleSwitchCustomer"
              color="teal"
              view="outlined"
              dense
              >Switch Customer
            </Button>
          </div>
        </div>
      </div>
      <div v-else-if="isShowCustomerForm">
        <BulkQuoteCustomerForm
          ref="quoteCustomerForm"
          v-model:data="customerData"
          @submit="isSubmitted = true"
          @cancel="handleCancelCustomerForm"
        />
      </div>
      <div v-else>
        <CustomerSearch class="mb-4" @select-customer="handleSelectCustomer">
          <template #prepend>
            <button
              @click="isShowCustomerForm = true"
              class="bulk-quote-customer__add-customer-button"
              tabindex="0"
            >
              <i class="material-icons">add</i>
              <span>Add New Customer</span>
            </button>
          </template>
        </CustomerSearch>
      </div>
    </transition>
  </div>
</template>

<script>
import { computed, defineComponent, ref } from 'vue'

import { useUser } from 'composable/user'

import Button from 'bbUi/components/button/button.vue'
import CustomerSearch from 'components/Customer/CustomerSearch.vue'
import BulkQuoteCustomerForm from 'components/BulkQuote/Customer/BulkQuoteCustomerForm.vue'

import { phoneNumber } from 'utils/number'
import moment from 'moment'

export default defineComponent({
  name: 'BulkQuoteCustomer',
  components: {
    Button,
    CustomerSearch,
    BulkQuoteCustomerForm
  },
  props: {
    data: {
      type: Object,
      default: () => ({})
    }
  },
  emits: ['update:data', 'submit'],
  setup(props, { emit }) {
    const { me } = useUser()
    const customerData = computed({
      get: () => props.data,
      set: (value) => {
        emit('update:data', value)
      }
    })
    const quoteCustomerForm = ref()
    const isShowCustomerForm = ref(false)
    const isSubmitted = ref(false)

    const formattedPhoneNumber = computed(() =>
      customerData.value.shipping_phone
        ? phoneNumber(customerData.value.shipping_phone)
        : '-'
    )
    const formattedExpectedDateQuote = computed(() =>
      customerData.value.shipping_expected_date_quote
        ? moment(customerData.value.shipping_expected_date_quote).format(
            me.value.date_format || 'YYYY-MM-DD'
          )
        : '-'
    )

    const unsetCustomerData = () => {
      const currentCustomerData = { ...customerData.value }
      customerData.value = Object.fromEntries(
        Object.entries(currentCustomerData).map(([key, _]) => [key, null])
      )
    }
    const handleSelectCustomer = (selectedCustomer) => {
      const {
        address_text: customerAddressText = null,
        address1: customerAddress1 = null,
        suite: customerAddressSuite = null,
        city: customerAddressCity = null,
        state: customerAddressState = null,
        zip: customerAddressZip = null,
        country: customerAddressCountry = null
      } = selectedCustomer?.bulk_quote_address || {}
      customerData.value = {
        ...customerData.value,
        id: selectedCustomer.customer_id,
        company: selectedCustomer.company,
        email: selectedCustomer.email,
        full_name: selectedCustomer.customer_name,
        shipping_phone: selectedCustomer.phone_number,
        shipping_address_text: customerAddressText,
        shipping_address1: customerAddress1,
        shipping_suite: customerAddressSuite,
        shipping_city: customerAddressCity,
        shipping_state: customerAddressState,
        shipping_zip: customerAddressZip,
        shipping_country: customerAddressCountry
      }
      isShowCustomerForm.value = true
    }
    const handleUpdateCustomerData = (formData) => {
      customerData.value = formData
    }
    const handleEditCustomerData = () => {
      isSubmitted.value = false
      isShowCustomerForm.value = true
    }
    const handleSwitchCustomer = () => {
      isSubmitted.value = false
      isShowCustomerForm.value = false
      unsetCustomerData()
    }
    const handleCancelCustomerForm = () => {
      isSubmitted.value = false
      isShowCustomerForm.value = false
      unsetCustomerData()
    }

    return {
      customerData,
      isSubmitted,
      quoteCustomerForm,
      isShowCustomerForm,
      formattedPhoneNumber,
      formattedExpectedDateQuote,
      handleSelectCustomer,
      handleUpdateCustomerData,
      handleEditCustomerData,
      handleSwitchCustomer,
      handleCancelCustomerForm,
      phoneNumber
    }
  }
})
</script>

<style scoped lang="scss">
.bulk-quote-customer {
  &__add-customer-button {
    @apply flex flex-wrap items-center w-full hover:bg-grey-lighten  focus:bg-grey-lighten p-3;
  }
}
</style>
