<template>
  <BDropdown block :center="false" prevent-toggle>
    <Input
      :model-value="searchQuery"
      @update:model-value="handleSearchCustomer"
      placeholder="Find or add a customer"
      block
      icon
      icon-left
    >
      <template #icon>
        <i class="material-icons text-grey-02">search</i>
      </template>
    </Input>
    <template #dropdown="{ collapse }">
      <div class="customer-search-dropdown">
        <div class="customer-search-dropdown__item-container">
          <slot name="prepend"></slot>
        </div>
        <small class="block font-bold mx-3 my-3">Recent</small>
        <div
          v-loading="loading || loadingCustomerSearch.getCustomerList"
          :style="{ minHeight: '50px', maxHeight: '300px' }"
        >
          <template
            v-if="
              searchQuery &&
              customerList.length < 1 &&
              !loadingCustomerSearch.getCustomerList
            "
          >
            <div class="p-4">
              <p class="text-grey-02">
                Customer not found. Please add it as a new customer
              </p>
            </div>
          </template>
          <template v-else>
            <div
              v-for="customer in customerList"
              :key="customer.id"
              class="customer-search-dropdown__item-container"
            >
              <button
                @click="
                  () => {
                    handleSelectCustomer(customer)
                    collapse()
                  }
                "
                class="customer-search-dropdown__item"
                tabindex="0"
              >
                <slot v-bind="{ customer }">
                  <span>{{ customer.customer_name }}</span>
                  <span class="text-grey-02">{{ customer.email }}</span>
                </slot>
              </button>
            </div>
          </template>
        </div>
      </div>
    </template>
  </BDropdown>
</template>

<script>
import { defineComponent, onMounted, watch } from 'vue'
import useCustomerSearch from 'composable/customer/customerSearch'

import Input from 'bbUi/components/input/input.vue'
import BDropdown from 'components/BDropdown/BDropdown.vue'

export default defineComponent({
  name: 'CustomerSearch',
  components: {
    Input,
    BDropdown
  },
  props: {
    multiple: {
      type: Boolean,
      default: false
    },
    loading: {
      type: Boolean,
      default: false
    }
  },
  emits: ['selectCustomer'],
  setup(props, { emit }) {
    const {
      isMultiSelect,
      searchQuery,
      customerList,
      loading: loadingCustomerSearch,
      currentSelectedCustomer,
      selectCustomer,
      searchCustomer
    } = useCustomerSearch({ type: 'bulk-quote' })
    const handleSelectCustomer = (customer) => {
      const { customer_name: customerName = '' } = customer || {}
      searchQuery.value = customerName
      selectCustomer(customer)
      emit('selectCustomer', currentSelectedCustomer.value)
    }
    const handleSearchCustomer = async (query = '') => {
      searchQuery.value = query
      await searchCustomer()
    }
    watch(
      () => props.multiple,
      (isMultipleSelect) => {
        isMultiSelect.value = isMultipleSelect
      },
      {
        immediate: true
      }
    )
    onMounted(async () => {
      await searchCustomer()
    })
    return {
      searchQuery,
      customerList,
      loadingCustomerSearch,
      handleSelectCustomer,
      handleSearchCustomer
    }
  }
})
</script>

