.customer-search-dropdown {
  &__section {
    &:not(:last-child) {
      @apply border-b border-grey-01;
    }
  }

  &__item {
    @apply flex flex-wrap items-center gap-3 p-3 w-full text-left;
    &:active,
    &:focus {
      @apply bg-grey-lighten;
    }
  }

  &__add-item {
    @apply flex flex-wrap items-center gap-3 p-3 w-full text-left;
  }

  &__item-container {
    &:not(:last-child) {
      @apply border-b border-grey-01;
    }
  }

  button {
    &:active,
    &:focus,
    &:hover {
      @apply bg-grey-lighten;
    }
  }
}
</style>
