<template>
  <div>
    <div class="grid grid-cols-1 gap-2">
      <Checkbox
        :model-value="isUseMemo"
        @click.prevent="toggleMemoCheckbox"
        class="font-semibold"
      >
        Memo
      </Checkbox>
      <transition name="slide-fade">
        <div v-if="isUseMemo" class="pl-9">
          <p class="mb-3">{{ memo }}</p>
          <Button
            @click="data.isShowModal = true"
            dense
            color="teal"
            view="outlined"
          >
            Edit
          </Button>
        </div>
      </transition>
    </div>

    <Modal v-model="data.isShowModal" title="Memo">
      <Input v-model="memo" type="textarea" block rows="6" placeholder="Memo" />
      <template #footer>
        <div class="flex flex-wrap justify-between items-center">
          <div>
            <a
              class="py-3 px-4"
              href=""
              @click.prevent="data.isShowModal = false"
              >Cancel</a
            >
          </div>
          <div>
            <Button @click="handleSaveMemo" color="teal">Confirm</Button>
          </div>
        </div>
      </template>
    </Modal>
  </div>
</template>

<script>
import { computed, defineComponent, reactive } from 'vue'

import Checkbox from 'bbUi/components/checkbox/checkbox.vue'
import Modal from 'bbUi/components/modal/modal.vue'
import Input from 'bbUi/components/input/input.vue'
import Button from 'bbUi/components/button/button.vue'

export default defineComponent({
  name: 'BulkQuoteMemo',
  components: {
    Checkbox,
    Modal,
    Input,
    Button
  },
  props: {
    modelValue: {
      type: String
    },
    isUseMemo: {
      type: Boolean,
      default: false
    }
  },
  emits: ['update:modelValue', 'update:isUseMemo'],
  setup(props, { emit }) {
    const data = reactive({
      isUseMemo: false,
      isShowModal: false,
      memo: null
    })

    const memo = computed({
      get: () => (props.modelValue && props.modelValue) || data.memo,
      set: (newValue = '') => {
        emit('update:modelValue', newValue)
        data.memo = newValue
      }
    })
    const isUseMemo = computed({
      get: () => (props.isUseMemo && props.isUseMemo) || data.isUseMemo,
      set: (isUse = false) => {
        emit('update:isUseMemo', isUse)
        data.isUseMemo = isUse
      }
    })

    const toggleMemoCheckbox = () => {
      if (!memo.value) {
        data.isShowModal = true
        return
      }
      isUseMemo.value = !isUseMemo.value
    }
    const handleSaveMemo = () => {
      isUseMemo.value = true
      data.isShowModal = false
    }

    return {
      isUseMemo,
      data,
      memo,
      handleSaveMemo,
      toggleMemoCheckbox
    }
  }
})
</script>

<style scoped lang="scss"></style>