<template>
  <form @submit.prevent="submit">
    <div class="grid grid-cols-1 gap-6">
      <div>
        <Input
          :model-value="data.company"
          @update:model-value="handleUpdateData('company', $event)"
          label="Business/School Name"
          placeholder="Business/school name"
          :view="validation.company.$invalid ? 'danger' : 'default'"
          block
        />
        <small
          v-for="(error, errorIndex) of validation.company.$errors"
          :key="errorIndex"
          class="block text-red-04 my-2"
        >
          {{ error.$message }}
        </small>
      </div>
      <div>
        <Input
          :model-value="data.full_name"
          @update:model-value="handleUpdateData('full_name', $event)"
          label="Primary Contact"
          placeholder="Primary contact"
          :view="validation.full_name.$invalid ? 'danger' : 'default'"
          block
        />
        <small
          v-for="(error, errorIndex) of validation.full_name.$errors"
          :key="errorIndex"
          class="block text-red-04 my-2"
        >
          {{ error.$message }}
        </small>
      </div>
      <div>
        <Input
          :model-value="data.email"
          @update:model-value="handleUpdateData('email', $event)"
          :disabled="hasCustomerId"
          label="Email"
          placeholder="Email"
          :view="validation.email.$invalid ? 'danger' : 'default'"
          block
        />
        <small
          v-for="(error, errorIndex) of validation.email.$errors"
          :key="errorIndex"
          class="block text-red-04 my-2"
        >
          {{ error.$message }}
        </small>
      </div>
      <div>
        <InputPhone
          :model-value="data.shipping_phone"
          @update:model-value="handleUpdateData('shipping_phone', $event)"
          block
        />
        <small
          v-for="(error, errorIndex) of validation.shipping_phone.$errors"
          :key="errorIndex"
          class="block text-red-04 my-2"
        >
          {{ error.$message }}
        </small>
      </div>
      <div>
        <Input
          v-gmap="addressBindingOptions"
          :model-value="data.shipping_address_text"
          @update:model-value="
            handleUpdateData('shipping_address_text', $event)
          "
          :view="
            validation.shipping_address_text.$invalid ? 'danger' : 'default'
          "
          label="Shipping from Address"
          placeholder="Street address"
          block
        />
        <small class="block text-grey-02 my-2"
          >Street address, P.O box, company name, c/o.</small
        >
        <small
          v-for="(error, errorIndex) of validation.shipping_address_text
            .$errors"
          :key="errorIndex"
          class="block text-red-04 my-2"
        >
          {{ error.$message }}
        </small>
      </div>
      <div>
        <Input
          :model-value="data.shipping_suite"
          @update:model-value="handleUpdateData('shipping_suite', $event)"
          placeholder="Apartment, suite, unit, building, floor, etc"
          block
        />
        <small class="block text-grey-02 my-2">
          Apartment, suite, unit, building, floor, etc.
        </small>
      </div>
      <div>
        <label class="font-bold">Expected Shipped Date</label>
        <DatePicker
          :model-value="data.shipping_expected_date_quote"
          @update:model-value="
            handleUpdateData('shipping_expected_date_quote', $event)
          "
          v-bind="dateOptions"
          :format="dateFormat"
          type="date"
          placeholder="Select expected date"
          class="block"
        />
        <small
          v-for="(error, errorIndex) of validation.shipping_expected_date_quote
            .$errors"
          :key="errorIndex"
          class="block text-red-04 my-2"
        >
          {{ error.$message }}
        </small>
      </div>
      <div>
        <Button dense color="teal">Save</Button>
        <a @click.prevent="$emit('cancel')" href="" class="px-4 py-2 mx-4">Cancel</a>
      </div>
    </div>
  </form>
</template>

<script>
import { computed, defineComponent, ref } from 'vue'
import { useForm } from 'composable/helper/form'
import { useUser } from 'composable/user'

import Input from 'bbUi/components/input/input.vue'
import Button from 'bbUi/components/button/button.vue'
import InputPhone from 'components/InputPhone.vue'

import { email, helpers, required } from '@vuelidate/validators'

export default defineComponent({
  name: 'BulkQuoteCustomerForm',
  components: {
    Input,
    Button,
    InputPhone
  },
  props: {
    data: {
      type: Object
    }
  },
  emits: ['update:data', 'submit', 'cancel'],
  setup(props, { emit }) {
    const { me, dateByTimezone } = useUser()

    const formData = ref({
      id: null,
      full_name: null,
      email: null,
      company: null,
      shipping_address1: null,
      shipping_address2: null,
      shipping_address_text: null,
      shipping_suite: null,
      shipping_city: null,
      shipping_phone: null,
      shipping_state: null,
      shipping_zip: null,
      shipping_country: null,
      shipping_expected_date_quote: null
    })

    const data = computed({
      get: () => (props.data && props.data) || formData.value,
      set: (value) => {
        emit('update:data', value)
        formData.value = value
      }
    })
    const dateOptions = computed(() => ({
      disabledDate(time) {
        return time.getTime() < dateByTimezone.value.getTime()
      }
    }))
    const dateFormat = computed(() =>
      me.value?.date_format ? me.value.date_format : 'DD/MM/YYYY'
    )
    const hasCustomerId = computed(() => !!data.value.id)

    const submit = () => {
      const hasInvalidField = !validate()
      if (hasInvalidField) return
      emit('submit', data.value)
    }
    const handleUpdateData = (key, value) => {
      data.value = { ...data.value, [key]: value }
    }

    const rules = {
      full_name: {
        required: helpers.withMessage('Primary contact is required', required)
      },
      email: {
        required: helpers.withMessage('Email is required', required),
        email: helpers.withMessage('Please enter a valid email address', email)
      },
      company: {
        required: helpers.withMessage(
          'Business/School Name is required',
          required
        )
      },
      shipping_phone: {
        required: helpers.withMessage('Shipping Phone is required', required)
      },
      shipping_address_text: {
        required: helpers.withMessage('Street address is required', required)
      },
      shipping_expected_date_quote: {
        required: helpers.withMessage(
          'Expected Shipped Date is required',
          required
        )
      }
    }
    const { validation, validate } = useForm(data, rules)

    const addressBindingOptions = {
      route: 'shipping_address1',
      locality: 'shipping_city',
      administrative_area_level_1: 'shipping_state',
      formatted_address: 'shipping_address_text',
      country: 'shipping_country',
      postal_code: 'shipping_zip',
      geo: {
        lat: 'lat',
        lng: 'lon'
      },
      noComponentRestriction: true
    }

    return {
      data,
      handleUpdateData,
      submit,
      validation,
      addressBindingOptions,
      dateOptions,
      dateFormat,
      hasCustomerId
    }
  }
})
</script>

<style scoped lang="scss">
.bulk-quote-customer-form {
}

:deep(.btn-dense) {
  @apply px-4 #{!important};
}
</style>
