<template>
  <div class="grid gap-2">
    <Checkbox v-model="isUseCustomField" class="font-semibold"
      >Custom field
    </Checkbox>
    <transition name="slide-fade">
      <div v-if="isUseCustomField" class="grid grid-cols-2 gap-4 pl-9">
        <div>
          <span class="font-semibold">Field Name</span>
        </div>
        <div>
          <span class="font-semibold">Field Values</span>
        </div>
        <template
          v-for="(field, fieldIndex) of formattedFields"
          :key="fieldIndex"
        >
          <div>
            <Input
              :model-value="field.label"
              @update:model-value="updateFieldLabel(fieldIndex, $event)"
              maxlength="50"
              block
            />
          </div>
          <div class="flex gap-4 items-center">
            <Input
              :model-value="field.value"
              @update:model-value="updateFieldValue(fieldIndex, $event)"
              block
            />
            <a
              v-if="isRemovableField"
              class="px-4 py-2"
              href="#"
              @click.prevent="removeField(fieldIndex)"
            >
              <i class="material-icons">close</i>
            </a>
          </div>
        </template>
        <div class="col-span-2">
          <Button
            :disabled="isDisabledAddCustomRow"
            @click="addField"
            color="teal"
            view="outlined"
            dense
            >Add Custom Row
          </Button>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
import { computed, defineComponent, onMounted, reactive } from 'vue'

import Checkbox from 'bbUi/components/checkbox/checkbox.vue'
import Input from 'bbUi/components/input/input.vue'
import Button from 'bbUi/components/button/button.vue'

export default defineComponent({
  name: 'BulkQuoteCustomFields',
  components: {
    Input,
    Checkbox,
    Button
  },
  props: {
    modelValue: {
      type: Array
    },
    label: {
      type: [String],
      default: 'name'
    },
    value: {
      type: [String],
      default: 'value'
    },
    isUseCustomField: {
      type: Boolean,
      default: false
    }
  },
  emits: ['update:modelValue', 'update:isUseCustomField'],
  setup(props, { emit }) {
    const data = reactive({
      isUseCustomField: false,
      fields: []
    })

    const isUseCustomField = computed({
      get: () =>
        (props.isUseCustomField && props.isUseCustomField) ||
        data.isUseCustomField,
      set: (isUse = false) => {
        emit('update:isUseCustomField', isUse)
        data.isUseCustomField = isUse
      }
    })
    const fields = computed({
      get: () => (props.modelValue && props.modelValue) || data.fields,
      set: (updatedFields = []) => {
        emit('update:modelValue', updatedFields)
        data.fields = updatedFields
      }
    })
    const formattedFields = computed(() => formatFields(fields.value))
    const isRemovableField = computed(() => {
      return fields.value.length > 1
    })
    const isDisabledAddCustomRow = computed(() => fields.value.length > 4)

    const formatFields = (fields) =>
      fields.map((field) => ({
        label: field[props.label],
        value: field[props.value]
      }))
    const addField = () => {
      fields.value = [
        ...fields.value,
        {
          [props.label]: '',
          [props.value]: ''
        }
      ]
    }
    const removeField = (fieldIndex) => {
      if (!isRemovableField.value) return
      const currentFields = [...fields.value]
      currentFields.splice(fieldIndex, 1)
      fields.value = currentFields
    }
    const updateFieldLabel = (fieldIndex, value) => {
      const currentFields = [...fields.value]
      currentFields[fieldIndex][props.label] = value
      fields.value = currentFields
    }
    const updateFieldValue = (fieldIndex, value) => {
      const currentFields = [...fields.value]
      currentFields[fieldIndex][props.value] = value
      fields.value = currentFields
    }

    onMounted(() => {
      const isEmptyFields = fields.value.length < 1
      if (isEmptyFields) addField()
    })
    return {
      formattedFields,
      isUseCustomField,
      isRemovableField,
      isDisabledAddCustomRow,
      addField,
      removeField,
      updateFieldLabel,
      updateFieldValue
    }
  }
})
</script>

<style scoped lang="scss"></style>
