<template>
  <div>
    <hr class="border-2 border-teal-04" />
    <div
      class="p-4 grid gap-4 box-border overflow-x-auto"
      :style="{ maxHeight: '70vh' }"
    >
      <div class="flex items-start justify-between mb-4">
        <h2 class="font-medium text-3xl">Contract Agreement</h2>
        <div
          class="w-16 h-16"
          :style="{
            backgroundImage: `url(${companyLogo})`,
            backgroundSize: 'contain',
            backgroundPosition: 'center',
            backgroundRepeat: 'no-repeat'
          }"
        ></div>
      </div>
      <template v-if="isShowCustomerData">
        <div class="flex justify-between gap-8">
          <div class="text-grey-03 font-normal">
            <p class="font-semibold text-black">To:</p>
            <p>{{ customerFullName }}</p>
            <p>{{ customerPhoneNumber }}</p>
            <p>{{ address }}</p>
          </div>
          <div class="text-right text-grey-03 font-normal">
            <p class="font-semibold text-black">Buyback ID# AS53780</p>
            <p class="truncate">Expected shipped: {{ expectedShipping }}</p>
          </div>
        </div>
        <hr class="border border-grey-lighten" />
        <div>
          <p class="font-semibold text-black">Rental Agreement:</p>
          <p class="text-grey-03 font-normal">I agree and hereby certify that all information provided is accurate</p>
        </div>
        <div class="mt-2">
          <p class="font-semibold text-black">Signature:</p>
          <div class="py-6"></div>
        </div>
        <hr class="border border-grey-lighten" />
      </template>
      <div>
        <BulkQuoteItemTablePreview
          v-for="itemGroup in items"
          :key="itemGroup.id"
          :items="itemGroup.items"
          item-name-key="product_name"
          :conditions-key="conditionsKey"
        />
      </div>
      <div v-if="isShowMemo">
        <p>{{ memo }}</p>
      </div>
      <template v-if="isUseCustomField">
        <div v-for="(field, fieldKey) in customFields" :key="fieldKey">
          <p class="font-semibold text-black">{{ field.name }}</p>
          <p>{{ field.value }}</p>
        </div>
      </template>
    </div>
  </div>
</template>

<script>
import { computed, defineComponent } from 'vue'

import { useUser } from 'composable/user'

import moment from 'moment/moment'
import { phoneNumber } from 'utils/number'

import BulkQuoteItemTablePreview from 'components/BulkQuote/Item/BulkQuoteItemTablePreview.vue'

export default defineComponent({
  name: 'BulkQuotePreview',
  components: {
    BulkQuoteItemTablePreview
  },
  props: {
    customer: {
      type: Object,
      default: () => ({})
    },
    items: {
      type: Array,
      default: () => []
    },
    memo: {
      type: String,
      default: ''
    },
    isUseMemo: {
      type: Boolean,
      default: false
    },
    customField: {
      type: Array,
      default: () => []
    },
    isUseCustomField: {
      type: Boolean,
      default: false
    },
    salesPerson: {
      type: Object,
      default: () => ({})
    }
  },
  setup(props) {
    const { me } = useUser()
    const expectedShipping = computed(() => {
      return props.customer?.shipping_expected_date_quote
        ? moment(props.customer.shipping_expected_date_quote).format(
            me.value.date_format || 'YYYY-MM-DD'
          )
        : null
    })
    const customerFullName = computed(() =>
      props.customer.full_name ? props.customer.full_name : '-'
    )
    const address = computed(() =>
      props.customer.shipping_address_text
        ? props.customer.shipping_address_text
        : null
    )
    const customerPhoneNumber = computed(() =>
      props.customer.shipping_phone
        ? phoneNumber(props.customer.shipping_phone)
        : null
    )
    const customFields = computed(() =>
      props.customField
        .map((field) => ({
          name: field.name,
          value: field.value
        }))
        .filter((field) => field.value)
    )
    const companyLogo = computed(() => me.value.logo)
    const isShowCustomerData = computed(() => {
      return (
        expectedShipping.value &&
        customerFullName.value &&
        address.value &&
        customerPhoneNumber.value
      )
    })
    const isShowMemo = computed(() => props.isUseMemo && props.memo.length > 0)

    const conditionsKey = (item) => {
      return item?.conditions ? item.conditions?.data || [] : []
    }

    return {
      customerFullName,
      expectedShipping,
      address,
      customerPhoneNumber,
      customFields,
      companyLogo,
      isShowCustomerData,
      isShowMemo,
      conditionsKey
    }
  }
})
</script>

<style scoped lang="scss"></style>
