import { computed, reactive, ref } from 'vue'
import store from 'store'
import debounce from 'utils/debounce'

const useCustomerSearch = (options = { type: 'bulk-quote' }) => {
  const isMultiSelect = ref(false)
  const searchQuery = ref(null)
  const customerList = ref([])
  const selectedCustomer = ref([])
  const loading = reactive({
    getCustomerList: false
  })

  const currentSelectedCustomer = computed(() =>
    isMultiSelect.value ? selectedCustomer.value : selectedCustomer.value[0]
  )

  const setCustomerList = (customers = []) => {
    customerList.value = customers
  }
  const getCustomerList = async () => {
    try {
      loading.getCustomerList = true
      const response = await store.dispatch('customer/all', {
        search: searchQuery.value,
        page: 1,
        limit: 5,
        status: 1,
        type: options.type ? options.type : 'bulk-quote'
      })
      const { data: customers } = response || {}
      setCustomerList(customers)
      loading.getCustomerList = false
      return true
    } catch (err) {
      if (err.message === 'canceled') {
        return
      }
      console.error('Error fetching customer list:', err)
      loading.getCustomerList = false
      return false
    }
  }
  const searchCustomer = debounce(async () => {
    await getCustomerList()
  }, 300)
  const selectCustomer = (customer) => {
    if (isMultiSelect.value) return
    selectedCustomer.value.push(customer)
  }
  const unsetSelectedCustomers = () => {
    selectedCustomer.value = []
  }
  return {
    isMultiSelect,
    searchQuery,
    customerList,
    selectedCustomer,
    loading,
    currentSelectedCustomer,
    getCustomerList,
    searchCustomer,
    selectCustomer,
    unsetSelectedCustomers
  }
}

export default useCustomerSearch
