<template>
  <div>
    <div class="flex flex-wrap items-center justify-between my-4">
      <Breadcrumb :backTo="{ name: 'layoutBulkQuote' }" :items="breadcrumbs">
        <template #page-title>
          <div class="flex gap-2 items-center flex-wrap">
            <h6>New Bulk Quote</h6>
            <div
              class="inline-flex px-4 py-2 bg-blue-lighten text-blue-04 rounded-full"
            >
              Beta
            </div>
          </div>
        </template>
      </Breadcrumb>
      <Button
        @click="handleSubmitQuote"
        :disabled="isSubmitQuoteDisabled || bulkQuoteLoading"
        color="teal"
        class="sticky top-96"
      >
        Continue
      </Button>
    </div>
    <div class="grid grid-cols-5 gap-6 my-8">
      <div class="grid grid-cols-1 gap-6 xl:col-span-3 col-span-5">
        <Card v-loading="bulkQuoteLoading">
          <template #header>
            <h2 class="text-xl font-medium">Customer Info</h2>
          </template>
          <div class="grid grid-cols-5">
            <div class="xl:col-span-3 col-span-5">
              <BulkQuoteCustomer
                ref="quoteCustomer"
                v-model:data="bulkQuoteData.customer"
              />
            </div>
          </div>
        </Card>
        <Card v-loading="bulkQuoteLoading">
          <template #header>
            <h2 class="text-xl font-medium">Add Item</h2>
          </template>
          <div>
            <div class="relative">
              <BulkQuoteItemPercentage
                v-if="bulkQuoteData.selected_products.length > 0"
                v-model.number="bulkQuoteData.quote_price_percentage"
                create
                class="my-4"
              /><!-- Block quote percentage when manually priced -->
              <div
                v-if="hasManuallyPricedItems"
                @click="confirmManuallyPricedModal = true"
                class="absolute left-0 top-0 w-full h-full cursor-pointer"
              ></div>
            </div>
            <BulkQuoteItem
              v-model:items="bulkQuoteData.selected_products"
              :percentage="bulkQuoteData.quote_price_percentage"
              class="my-4"
            />
            <template v-if="isAddingItem">
              <BulkQuoteItemSearch @select="handleSelectItem" class="my-4" />
            </template>
            <template v-else>
              <Button
                view="outlined"
                dense
                color="teal"
                @click="isAddingItem = true"
              >
                Add Item
              </Button>
            </template>
          </div>
        </Card>
        <Card v-loading="bulkQuoteLoading">
          <template #header>
            <h2 class="text-xl font-medium">Agent Detail</h2>
          </template>
          <div class="grid grid-cols-5">
            <div class="xl:col-span-3 col-span-5">
              <BulkQuoteSalesPerson
                ref="quoteSalesPerson"
                v-model:data="bulkQuoteData.sales_person"
              />
            </div>
          </div>
        </Card>
        <Card v-loading="bulkQuoteLoading">
          <template #header>
            <h2 class="text-xl font-medium">Additional Options</h2>
          </template>
          <div class="grid grid-cols-5 mb-4">
            <div class="col-span-5 mb-4">
              <p>
                Customize invoices with additional fields to better match your
                compliance requirements and business needs.
              </p>
            </div>
            <div class="xl:col-span-3 col-span-5 mb-4">
              <BulkQuoteMemo
                v-model="bulkQuoteData.additional_options.memo"
                v-model:is-use-memo="bulkQuoteData.is_use_memo"
              />
            </div>
            <div class="col-span-5 mb-4">
              <BulkQuoteCustomField
                v-model="bulkQuoteData.additional_options.custom_fields"
                v-model:is-use-custom-field="bulkQuoteData.is_use_custom_fields"
              />
            </div>
            <div
              v-if="rulesShipping?.is_enable_multiple_label == 1"
              class="col-span-5">
              <BulkQuoteAdditionalShippingLabel
                v-model="bulkQuoteData.additional_options.add_shipping_label_number"
                v-model:is-use-additional-shipping-label="bulkQuoteData.additional_options.is_use_additional_shipping_label"
                :limit-exceed="rulesShipping.label_qty"
                @update:modelValue="handleChangeAdditionalShpping"
              />
            </div>
          </div>
        </Card>
      </div>
      <div class="xl:col-span-2 col-span-5">
        <Card class="sticky top-32">
          <template #header>
            <h2 class="text-xl font-medium">Preview Contract</h2>
          </template>
          <div>
            <BulkQuotePreview
              :customer="bulkQuoteData.customer"
              :percentage="bulkQuoteData.quote_price_percentage"
              :items="bulkQuoteData.selected_products"
              :memo="bulkQuoteData.additional_options.memo"
              :is-use-memo="bulkQuoteData.is_use_memo"
              :custom-field="bulkQuoteData.additional_options.custom_fields"
              :is-use-custom-field="bulkQuoteData.is_use_custom_fields"
              :sales-person="bulkQuoteData.sales_person"
            />
          </div>
        </Card>
      </div>
    </div>
    <Modal
      v-model="confirmManuallyPricedModal"
      size="xs"
      hide-header
      hide-footer
    >
      <div class="text-center py-10">
        <img
          class="mb-10 mx-auto"
          src="@/assets/icon/attention-warning.svg"
          alt="Success Icon"
        />
        <div class="mt-8 mb-10">
          <h5 class="mb-4 font-medium">Attention!</h5>
          <p class="my-5">
            There are prices that were changed manually. Manually priced items
            will be returned to the original price before using this function.
          </p>
        </div>
        <Button
          @click="
            () => {
              resetManuallyPricedItems()
              confirmManuallyPricedModal = false
            }
          "
          color="teal"
          class="mr-3"
          >Proceed
        </Button>
        <a @click.prevent="confirmManuallyPricedModal = false" class="py-4 px-6">
          Cancel
        </a>
      </div>
    </Modal>
  </div>
</template>
<script>
import { computed, defineComponent, ref, onMounted, nextTick } from 'vue'
import { useRouter } from 'vue-router'

import { useCreateBulkQuote } from 'composable/bulkquote'
import { useManageShipping } from 'composable/setting'

import Card from 'bbUi/components/card/card.vue'
import Button from 'bbUi/components/button/button.vue'
import Modal from 'bbUi/components/modal/modal.vue'
import Breadcrumb from 'components/Breadcrumb.vue'

import BulkQuoteCustomer from 'components/BulkQuote/Customer/BulkQuoteCustomer.vue'
import BulkQuoteSalesPerson from 'components/BulkQuote/SalesPerson/BulkQuoteSalesPerson.vue'
import BulkQuoteMemo from 'components/BulkQuote/Additional/BulkQuoteMemo.vue'
import BulkQuoteCustomField from 'components/BulkQuote/Additional/BulkQuoteCustomField.vue'
import BulkQuoteItem from 'components/BulkQuote/Item/BulkQuoteItem.vue'
import BulkQuoteItemSearch from 'components/BulkQuote/Item/BulkQuoteItemSearch.vue'
import BulkQuoteItemPercentage from 'components/BulkQuote/Item/BulkQuoteItemPercentage.vue'
import BulkQuotePreview from 'components/BulkQuote/BulkQuotePreview.vue'
import BulkQuoteAdditionalShippingLabel from 'components/BulkQuote/Additional/BulkQuoteAdditionalShippingLabel.vue'

export default defineComponent({
  name: 'BulkQuoteCreate',
  components: {
    Card,
    Button,
    Modal,
    Breadcrumb,
    BulkQuoteCustomer,
    BulkQuoteSalesPerson,
    BulkQuoteMemo,
    BulkQuoteCustomField,
    BulkQuoteItem,
    BulkQuoteItemSearch,
    BulkQuoteItemPercentage,
    BulkQuotePreview,
    BulkQuoteAdditionalShippingLabel
  },
  setup() {
    const {
      data: bulkQuoteData,
      loading: bulkQuoteLoading,
      isSubmitQuoteDisabled,
      confirmManuallyPricedModal,
      hasManuallyPricedItems,
      addProduct,
      createBulkQuote,
      resetManuallyPricedItems
    } = useCreateBulkQuote()
    const manageShipping = useManageShipping()
    const router = useRouter()
    const rulesShipping = ref({
      label_qty: 1,
      is_enable_multiple_label: 0
    })
    const quoteCustomer = ref()
    const quoteSalesPerson = ref()
    const isAddingItem = ref(true)
    const hasAddedItem = computed(
      () => bulkQuoteData.selected_products.length > 0
    )
    const handleSelectItem = (selectedItem) => {
      addProduct(selectedItem)
      isAddingItem.value = false
    }
    const handleSubmitQuote = async () => {
      const response = await createBulkQuote()
      if (!response) return
      const { data = { box_id: null, order_id: null } } = response
      await nextTick()
      bulkQuoteLoading.value = true
      await router.push({
        name: 'bulkQuoteDetail',
        params: {
          buybackId: data.box_id
        }
      })
    }
    const handleLoadShippingRules = async () => {
      await manageShipping.actionAll(true, (res) => {
        const rulesLabels = res.param?.shipping_rules_multiple_label ?? []
        if (rulesLabels.length) {
          const selected = rulesLabels.find(i => i.offer_type === 'Bulk Offer')
          rulesShipping.value = {
            ...(selected ?? {}),
            label_qty: Number(selected?.label_qty) ?? 1,
            is_enable_multiple_label: res.param?.is_enable_multiple_label
          }
        } else rulesShipping.value = {
          is_enable_multiple_label: 0,
          label_qty: 1
        }
      })
    }
    const handleChangeAdditionalShpping = async (e) => {
      bulkQuoteData.additional_options.add_shipping_label_number = e
    }
    const breadcrumbs = [
      {
        text: 'Bulk Quote',
        to: {
          name: 'layoutBulkQuote'
        }
      },
      {
        text: 'Create Bulk Quote'
      }
    ]

    onMounted(async () => {
      await handleLoadShippingRules()
    })

    return {
      rulesShipping,
      quoteCustomer,
      quoteSalesPerson,
      bulkQuoteData,
      bulkQuoteLoading,
      isAddingItem,
      isSubmitQuoteDisabled,
      hasAddedItem,
      breadcrumbs,
      confirmManuallyPricedModal,
      hasManuallyPricedItems,
      resetManuallyPricedItems,
      handleSelectItem,
      handleSubmitQuote,
      handleLoadShippingRules,
      handleChangeAdditionalShpping
    }
  }
})
</script>
